#masthead{
  display: flex;
  flex-direction: row;
  background-color:orange;
}
#masthead,#site-navigation{
    display: flex;
    align-items: center;
  .menu{
    ul{
      li{
        list-style:none;
      }
    }
  }
}

#page-wrapper, #single-wrapper, #archive-wrapper {
  padding: 60px 0;
  position: relative;
  width: 100%;
  z-index: 2;
  min-height: 60vh;

  .entry-content {
    & > .kc_row {
      float: none;
    }


    a {
      text-decoration: underline;

      &:hover {
        text-decoration: none;
      }
    }

    ul, ol, dl {
      padding-left: 20px;

      li {
        margin-bottom: 15px;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    .f-img-wrapper {
      height: 400px;
      width: 100%;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: 50% 50%;
      }
    }

  }

}
